@import '../../scss/mixins';
@import '../../scss/variables';
@import '../../scss/tipography';

.detail__wrapper{
  @include center; 

}
.detail__info{
  width: 300px; 
  background-color: $semi-blue; 
  color: $bright-green; 
  padding: 10px;
  font-size: 16px; 
}
.detail__img{
height: 300px;  
}
.char__status__title{
  padding: 8px 0; 
}
.char__origin-title{
  padding: 8px 0; 
}
.char__origin-title{
  padding: 8px 0; 
}
.char__episodes-title{
  padding: 8px 0; 
}
.link__anchor{
  text-decoration: none; 
  color: $bright-green; 
  margin: 15px; 
  position: relative;
  width: 35px;
  height: 35px; 
  border-radius: 50%;
  background-color: $semi-blue;
  &:hover{
    color: $semi-blue;  
    background-color: $bright-green;
  }
  &::before{
    position: absolute; 
    top: 48%; 
    left: 45%; 
    transform: translate(-50%, -50%);
    content:'\f053';
    font-family: "Font Awesome 5 Pro";
    font-weight: 900;
    font-size: 20px;
  }
}

@media (min-width: 768px) {
  .detail__wrapper{
    min-height: 80vh; 
    flex-direction: row; 
    .img{
      border-radius: 0px; 
      margin: 0;
      width: 350px;
      height: 350px;  
    } 
    .detail__info{
      width: 350px;
      height: 350px; 
      background-color: $semi-blue; 
      color: $bright-green; 
      padding: 15px;
      font-size: 18px; 
    }
    .detail__img{
      height: 350px;
      width: 350px;    
    }
  }
  .link__anchor{
    margin: 20px; 
    width: 45px;
    height: 45px; 
    &::before{
      top: 48%; 
      left: 45%; 
      transform: translate(-50%, -50%);
      font-size: 30px;
    }
  }
}
@media (min-width: 1024px) {
  .detail__wrapper{ 
    .img{
      width: 400px;
      height: 400px;  
    } 
    .detail__info{
      width: 400px;
      height: 400px; 
      font-size: 20px; 
    }
    .detail__img{
      height: 400px;
      width: 400px;    
    }
  }
  .link__anchor{
    margin: 20px;
  }
}
.app__back{
  color: $bright-green; 
  text-decoration: underline; 
}
.noData{
  color: $bright-green; 
  text-transform: uppercase; 
  margin: 20px auto; 
}